<template>
    <section class="content">
        <div class="container-fluid">
            <div class="row">
                <div class="col-12">
                    <div class="card">
                        <div class="card-header">
                            <h3 class="card-title">Machine Wise Expenses Report</h3>
                            <div class="card-tools"></div>
                        </div>

                        <div class="card-body p-2">
                            <form autocomplete="off" @submit.prevent="getResults()">
                                <div class="form-row">
                                    <div class="col col-2">
                                        <v-select label="site_name" :reduce="(option) => option.id" :options="search_sites"
                                            placeholder="Select Site..." v-model="search.site_id" name="site_id">
                                        </v-select>
                                    </div>
                                    <div class="col col-2 pt-2">
                                        <datetime value-zone="Asia/Kolkata" v-model="search.date_from"
                                            input-class="form-control" placeholder="Created from date" />
                                    </div>
                                    <div class="col col-2 pt-2">
                                        <datetime value-zone="Asia/Kolkata" v-model="search.date_to"
                                            input-class="form-control" placeholder="Created to date" />
                                    </div>

                                    <div class="col col-3">
                                        <v-select label="name" :reduce="(option) => option.id" :options="categories"
                                            placeholder="Choose Category ..." v-model="search.category_id">\
                                        </v-select>
                                    </div>
                                    <div class="col col-3">
                                        <v-select label="name" :reduce="(option) => option.id" :options="sub_categories"
                                            placeholder="Choose description ..." v-model="search.sub_category_id">
                                        </v-select>
                                    </div>
                                    <div class="col col-2">
                                        <v-select label="name" :get-option-label="getLabel" :reduce="(option) => option.id"
                                            :options="search_machines" placeholder="Enter machines ..."
                                            v-model="search.machine_id" name="machine_id">
                                        </v-select>
                                    </div>
<!--
                                    <div class="col col-2">
                                        <v-select label="ticket_no" :reduce="(option) => option.id"
                                            :options="search_tickets" placeholder="Enter ticket ..."
                                            v-model="search.ticket_id" name="ticket_id">
                                        </v-select>
                                    </div> -->


                                    <div class="col  pt-2">
                                        <button type="submit" class="btn btn-success">
                                            Search
                                        </button>
                                        <button type="button" class="btn btn-info ml-2" @click="reset_filter()">
                                            Reset
                                        </button>
                                        <download-excel class="btn btn-warning ml-2" :fetch="export_csv"
                                            :fields="json_fields" :before-generate="startDownload"
                                            :before-finish="finishDownload" worksheet="My Worksheet" type="csv"
                                            name="machine_expenses.csv">
                                            <span class="labelText">
                                                <i class="fa fa-file-excel-o" aria-hidden="true"></i>
                                                Export as CSV</span>
                                        </download-excel>
                                    </div>
                                </div>
                            </form>
                        </div>

                        <!-- /.card-header -->
                        <div class="card-body table-responsive p-0 ticketTable">
                            <table class="table table-hover table-striped"  v-if="Tickets && Tickets.records">
                                <thead>
                                    <tr>
                                        <th>Machine Name</th>
                                        <th>FAR No</th>
                                        <th>Actual Material Expenses </th>
                                        <th>Actual Service Expenses</th>
                                        <th>Total Expenses</th>
                                    </tr>
                                </thead>
                                <tbody>
                                    <tr v-for="ticket in Tickets.records.data" :key="ticket.id" :class="ticket.row_color">
                                        <td>{{ ticket.name }}</td>
                                        <td>{{ ticket.far_no }}</td>
                                        <td>{{ ticket.material_expenses | toCurrency }}</td>
                                        <td>{{ ticket.service_expenses | toCurrency }}</td>
                                        <td>{{ (ticket.total_expenses) | toCurrency }}</td>

                                    </tr>
                                </tbody>
                                <tfoot>
                                    <tr class="table-dark">
                                        <td colspan="2">Total</td>
                                        <td><span v-if="Tickets && Tickets.actual_material_cost">{{
                                            Tickets.actual_material_cost | toCurrency }}</span></td>
                                        <td><span v-if="Tickets && Tickets.actual_service_cost">{{
                                            Tickets.actual_service_cost | toCurrency }}</span></td>
                                        <td>{{(Tickets.actual_material_cost+Tickets.actual_service_cost) | toCurrency }}</td>

                                    </tr>
                                </tfoot>
                            </table>
                        </div>
                        <!-- /.card-body -->
                        <div class="card-footer"  v-if="Tickets && Tickets.records">
                            <pagination :data="Tickets" @pagination-change-page="getResults"></pagination>
                        </div>
                    </div>
                    <!-- /.card -->
                </div>
            </div>
        </div>
    </section>
</template>

<script>
import { MonthPicker } from 'vue-month-picker'
import { MonthPickerInput } from 'vue-month-picker'
export default {
    name: "SiteWiseExpenses",
    components: {
        MonthPicker,
        MonthPickerInput
    },
    data() {
        return {
            pmModalData: "",
            created_users: [],
            search_machines: [],
            search_sites: [],
            search_tickets:[],
            search: {
                page: 1,
                machine_id: "",
                site_id: "",
                category_id: "",
                sub_category_id: "",
                date_from: "",
                date_to:"",
                report_type: "machine wise",
            },
            categories: [],
            sub_categories: [],
            sites: [],
            selected_machine: "",
            users: [],
            machines: [],
            Tickets: {},
            clearEmittedText: '',
            default_month: '',
            json_fields: {
                "Machine Name": "name",
                "FAR No": "far_no",
                "Actual Material Expenses": "material_expenses",
                "Actual Service Expenses": "service_expenses",
                "Total Expenses": "total_expenses",
            },
            json_meta: [
                [
                    {
                        key: "charset",
                        value: "utf-8",
                    },
                ],
            ],
        };
    },
    methods: {
        showClearText() {
            this.clearEmittedText = 'emitted'
            this.default_month = ''
            window.setTimeout(() => {
                this.clearEmittedText = null
            }, 1000)
        },
        async get_machine() {
            axios
                .get("/api/reports/machines", {
                    params: {
                        site_id: this.search.site_id,
                        category_id: this.search.category_id,
                        sub_category_id: this.search.sub_category_id,
                    },
                })
                .then((response) => {
                    this.search_machines = response.data.data;
                })
                .catch(() => console.warn("Oh. Something went wrong"));
        },
        reset_filter() {
            this.search = {
                page: 1,
                machine_id: "",
                site_id: "",
                category_id: "",
                sub_category_id: "",
                monthYear: "",
                report_type: "machine wise",
            };
            this.getResults();
        },
        UpdateValues(e) {
            this.search.tat_from = e.minValue;
            this.search.tat_to = e.maxValue;
        },
        // excel
        async export_csv() {
            this.search.report_type = 'machine wise export'
            const response = await axios.post("api/reports", this.search);
            this.search.report_type = 'machine wise'
            return response.data.data;
        },
        startDownload() {
            Swal.fire({
                title: "Please Wait !",
                html: "Data populating",
                allowOutsideClick: false,
                onBeforeOpen: () => {
                    Swal.showLoading();
                },
            });
        },
        finishDownload() {
            Swal.close();
        },

        getLabel(val) {
            if (typeof val === "object") {
                return val.far_no + " - " + val.name;
            } else {
                return val;
            }
        },
        getResults(page = 1) {
            this.$Progress.start();
            this.search.page = page;

            axios.post("/api/reports", this.search)
                .then(res => {
                    this.Tickets = res.data.data
                })
                .catch(err => {
                    console.error(err);
                })
            this.$Progress.finish();
        },

    },
    created() {
        this.getResults();
    },
    beforeCreate() {
        axios.get("api/get_pre_machine").then((res) => {
            this.categories = res.data.data.Category;
            this.sites = res.data.data.sites;
        });
        axios
            .get("/api/initial_ticket")
            .then((response) => {
                this.users = response.data.data.vendors;
                this.machines = response.data.data.machines;
                this.search_machines = response.data.data.machines;
                this.search_sites = response.data.data.sites;
                this.created_users = response.data.data.created_users;
            })
            .catch(() => console.warn("Oh. Something went wrong"));
    },
    watch: {
        async "search.sub_category_id"(n, o) {
            if (n) {
                this.get_machine();
            }
        },
        async "search.site_id"(n, o) {
            if (n) {
                this.get_machine();
            }
        },

        "search.category_id": {
            handler: function (n, o) {
                if (n) {
                    axios.get("api/category/" + n).then((res) => {
                        this.sub_categories = res.data.data;
                    });
                    this.get_machine();
                }
            },
            deep: true,
            initial: true,
        },
    },


};





</script>
